import React, { useEffect, useRef, useState } from 'react';
import { useStaticQuery, graphql, withPrefix } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import styled from 'styled-components';
import sr from '@utils/sr';
import { srConfig } from '@config';
import { Icon } from '@components/icons';
import { usePrefersReducedMotion } from '@hooks';
import ImageCarousel from '@components/carousel';
import ImageGallery from '@components/imageGallery';

const StyledProjectsGrid = styled.ul`
  ${({ theme }) => theme.mixins.resetList};

  a {
    position: relative;
    z-index: 1;
  }
`;

const StyledProject = styled.li`
  position: relative;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;

  @media (max-width: 768px) {
    ${({ theme }) => theme.mixins.boxShadow};
  }

  &:not(:last-of-type) {
    margin-bottom: 100px;

    @media (max-width: 768px) {
      margin-bottom: 70px;
    }

    @media (max-width: 480px) {
      margin-bottom: 30px;
    }
  }

  &:nth-of-type(odd) {
    .project-content {
      grid-column: 7 / -1;
      text-align: right;

      @media (max-width: 1080px) {
        grid-column: 5 / -1;
      }
      @media (max-width: 768px) {
        grid-column: 1 / -1;
        padding: 40px 40px 30px;
        text-align: left;
      }
      @media (max-width: 480px) {
        padding: 25px 25px 20px;
      }
    }
    .project-tech-list {
      justify-content: flex-end;

      @media (max-width: 768px) {
        justify-content: flex-start;
      }

      li {
        margin: 0 0 5px 20px;

        @media (max-width: 768px) {
          margin: 0 10px 5px 0;
        }
      }
    }
    .project-links {
      justify-content: flex-end;
      margin-left: 0;
      margin-right: -10px;

      @media (max-width: 768px) {
        justify-content: flex-start;
        margin-left: -10px;
        margin-right: 0;
      }
    }
    .project-image {
      grid-column: 1 / 8;

      @media (max-width: 768px) {
        grid-column: 1 / -1;
      }
    }
  }

  .project-content {
    position: relative;
    grid-column: 1 / 7;
    grid-row: 1 / -1;
    z-index: 5;
    background-color: transparent;

    @media (max-width: 1080px) {
      grid-column: 1 / 9;
    }

    @media (max-width: 768px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      grid-column: 1 / -1;
      padding: 40px 40px 30px;
      background-color: rgba(10, 25, 47, 0.85);
    }

    @media (max-width: 480px) {
      padding: 30px 25px 20px;
    }
  }

  .project-overline {
    margin: 10px 0;
    color: var(--green);
    font-family: var(--font-mono);
    font-size: var(--fz-xs);
    font-weight: 400;
  }

  .project-title {
    color: var(--lightest-slate);
    font-size: clamp(24px, 5vw, 28px);

    @media (min-width: 768px) {
      margin: 0 0 20px;
    }

    @media (max-width: 768px) {
      color: var(--white);

      a {
        position: static;

        &:before {
          content: '';
          display: block;
          position: absolute;
          z-index: 0;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
        }
      }
    }
  }

  .project-description {
    ${({ theme }) => theme.mixins.boxShadow};
    position: relative;
    z-index: 2;
    padding: 25px;
    border-radius: var(--border-radius);
    background-color: var(--light-navy);
    color: var(--light-slate);
    font-size: var(--fz-lg);

    @media (max-width: 768px) {
      padding: 20px 0;
      background-color: transparent;
      box-shadow: none;

      &:hover {
        box-shadow: none;
      }
    }

    a {
      ${({ theme }) => theme.mixins.inlineLink};
    }

    strong {
      color: var(--white);
      font-weight: normal;
    }
  }

  .project-tech-list {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    z-index: 2;
    margin: 25px 0 10px;
    padding: 0;
    list-style: none;

    li {
      margin: 0 20px 5px 0;
      color: var(--light-slate);
      font-family: var(--font-mono);
      font-size: var(--fz-xs);
      white-space: nowrap;
    }

    @media (max-width: 768px) {
      margin: 10px 0;

      li {
        margin: 0 10px 5px 0;
        color: var(--lightest-slate);
      }
    }
  }

  .project-links {
    display: flex;
    align-items: center;
    position: relative;
    margin-top: 10px;
    margin-left: -10px;
    color: var(--lightest-slate);

    a {
      ${({ theme }) => theme.mixins.flexCenter};
      padding: 10px;

      &.external {
        svg {
          width: 22px;
          height: 22px;
          margin-top: -4px;
        }
      }

      svg {
        width: 20px;
        height: 20px;
      }
    }

    .cta {
      ${({ theme }) => theme.mixins.smallButton};
      margin: 10px;
    }
  }

  .project-image {
    ${({ theme }) => theme.mixins.boxShadow};
    grid-column: 6 / -1;
    grid-row: 1 / -1;
    position: relative;
    z-index: 1;

    @media (max-width: 768px) {
      grid-column: 1 / -1;
      height: 100%;
      opacity: 0.15;
      position: absolute;
      width: 100%;
    }

    .carousel-wrapper {
      position: relative;
      width: 100%;
      height: 400px;
      border-radius: var(--border-radius);
      vertical-align: middle;
      overflow: hidden;
      cursor: pointer;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }

      @media (max-width: 768px) {
        opacity: 0.25;
        height: 100%;
      }

      &:hover {
        .gallery-hint {
          opacity: 1;
        }
      }

      .gallery-hint {
        position: absolute;
        top: 10px;
        right: 10px;
        background-color: rgba(10, 25, 47, 0.9);
        color: var(--green);
        padding: 8px 15px;
        border-radius: 4px;
        font-family: var(--font-mono);
        font-size: var(--fz-xs);
        opacity: 0;
        transition: opacity 0.3s ease;
        z-index: 10;
      }
    }

    a {
      width: 100%;
      height: 100%;
      background-color: transparent;
      border-radius: var(--border-radius);
      vertical-align: middle;

      &:hover,
      &:focus {
        background: transparent;
        outline: 0;

        &:before,
        .img {
          background: transparent;
          filter: none;
        }
      }

      &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 3;
        transition: var(--transition);
        background-color: rgba(10, 25, 47, 0.4);
        mix-blend-mode: multiply;
      }
    }
    .img {
      border-radius: var(--border-radius);
      transition: var(--transition);
      width: 100%;
      height: 400px;
      object-fit: cover;
      object-position: center;

      @media (max-width: 768px) {
        object-fit: cover;
        width: auto;
        height: 100%;
        filter: grayscale(100%) contrast(1) brightness(50%);
      }
    }
  }
`;

const Featured = () => {
  const data = useStaticQuery(graphql`
    {
      featured: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "/content/featured/" } }
        sort: { fields: [frontmatter___date], order: ASC }
      ) {
        edges {
          node {
            frontmatter {
              title
              coverGif
              cover {
                publicURL
                childImageSharp {
                  gatsbyImageData(
                    width: 500
                    quality: 90
                    placeholder: DOMINANT_COLOR
                    formats: [AUTO, WEBP, AVIF]
                  )
                }
              }
              images {
                publicURL
                childImageSharp {
                  gatsbyImageData(
                    width: 700
                    quality: 95
                    placeholder: DOMINANT_COLOR
                    formats: [AUTO, WEBP, AVIF]
                  )
                }
              }
              video {
                publicURL
              }
              tech
              github
              external
            }
            html
          }
        }
      }
    }
  `);

  const featuredProjects = data.featured.edges.filter(({ node }) => node);
  const revealTitle = useRef(null);
  const revealProjects = useRef([]);
  const prefersReducedMotion = usePrefersReducedMotion();
  const [galleryOpen, setGalleryOpen] = useState(false);
  const [galleryImages, setGalleryImages] = useState([]);
  const [galleryInitialIndex, setGalleryInitialIndex] = useState(0);

  const handleImageClick = (images, index = 0) => {
    if (images && images.length > 0) {
      setGalleryImages(images);
      setGalleryInitialIndex(index);
      setGalleryOpen(true);
    }
  };

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealTitle.current, srConfig());
    revealProjects.current.forEach((ref, i) => sr.reveal(ref, srConfig(i * 100)));
  }, []);

  return (
    <section id="projects">
      <h2 className="numbered-heading" ref={revealTitle}>
        My Projects
      </h2>

      <StyledProjectsGrid>
        {featuredProjects &&
          featuredProjects.map(({ node }, i) => {
            const { frontmatter, html } = node;
            const { external, title, tech, github, cover, images, coverGif, video } = frontmatter;
            const image = getImage(cover);

            // For Partnerly project, crop the cover image to smaller dimensions
            const isPartnerly = title === 'Partnerly - Partnership Management';
            const imageList = images ? images.map(img => getImage(img)).filter(Boolean) : [];

            // Get image URLs for gallery
            let imageUrls =
              images
                ?.map(
                  img =>
                    img?.publicURL || img?.childImageSharp?.gatsbyImageData?.images?.fallback?.src,
                )
                .filter(Boolean) || [];

            // Add video to gallery if exists
            if (video?.publicURL) {
              imageUrls = [...imageUrls, video.publicURL];
            }

            // Use coverGif from static folder if provided, otherwise check for publicURL
            const gifURL = coverGif
              ? withPrefix(`/${coverGif}`)
              : cover && cover.publicURL && !image
                ? withPrefix(cover.publicURL)
                : null;

            return (
              <StyledProject key={i} ref={el => (revealProjects.current[i] = el)}>
                <div className="project-content">
                  <div>
                    <p className="project-overline">Featured Project</p>

                    <h3 className="project-title">{title}</h3>

                    <div
                      className="project-description"
                      dangerouslySetInnerHTML={{ __html: html }}
                    />

                    {tech.length && (
                      <ul className="project-tech-list">
                        {tech.map((tech, i) => (
                          <li key={i}>{tech}</li>
                        ))}
                      </ul>
                    )}

                    <div className="project-links">
                      {github && (
                        <a href={github} aria-label="GitHub Link">
                          <Icon name="GitHub" />
                        </a>
                      )}
                      {external && (
                        <a href={external} aria-label="External Link" className="external">
                          <Icon name="External" />
                        </a>
                      )}
                    </div>
                  </div>
                </div>

                <div className="project-image">
                  {gifURL && imageUrls.length > 0 ? (
                    <div
                      className="carousel-wrapper"
                      onClick={() => handleImageClick(imageUrls, 0)}
                      role="button"
                      tabIndex={0}
                      onKeyPress={e => {
                        if (e.key === 'Enter' || e.key === ' ') {
                          handleImageClick(imageUrls, 0);
                        }
                      }}
                      style={{ cursor: 'pointer' }}
                    >
                      <img
                        src={gifURL}
                        alt={title}
                        className="img"
                        style={{ width: '100%', height: 'auto' }}
                      />
                      {imageUrls.length > 1 && (
                        <div className="gallery-hint">
                          <span role="img" aria-label="gallery">
                            
                          </span>{' '}
                          {imageUrls.length} items
                        </div>
                      )}
                    </div>
                  ) : gifURL ? (
                    <img
                      src={gifURL}
                      alt={title}
                      className="img"
                      style={{ width: '100%', height: 'auto' }}
                    />
                  ) : imageList.length > 0 ? (
                    <div
                      className="carousel-wrapper"
                      onClick={() => handleImageClick(imageUrls, 0)}
                      role="button"
                      tabIndex={0}
                      onKeyPress={e => {
                        if (e.key === 'Enter' || e.key === ' ') {
                          handleImageClick(imageUrls, 0);
                        }
                      }}
                    >
                      <ImageCarousel images={imageList} />
                      {imageUrls.length > 1 && (
                        <div className="gallery-hint">
                          <span role="img" aria-label="gallery">
                            
                          </span>{' '}
                          {imageUrls.length} items
                        </div>
                      )}
                    </div>
                  ) : image ? (
                    <GatsbyImage
                      image={image}
                      alt={title}
                      className="img"
                      style={isPartnerly ? { height: '350px', width: '100%' } : {}}
                      imgStyle={isPartnerly ? { objectFit: 'cover', objectPosition: 'center' } : {}}
                    />
                  ) : null}
                </div>
              </StyledProject>
            );
          })}
      </StyledProjectsGrid>

      {galleryOpen && (
        <ImageGallery
          images={galleryImages}
          initialIndex={galleryInitialIndex}
          onClose={() => setGalleryOpen(false)}
        />
      )}
    </section>
  );
};

export default Featured;
